import { StyledHeader, StyledSubheader, StyledSubheader3 } from '../styled';
import { events } from './eventData';
import './events.css'

function Event(props) {
  const {title, date, src, description} = props.event;
  return (
    <section className="event-section">
      <aside>
        <img src={src} alt={title}></img>
      </aside>
      <article>
        <StyledSubheader>{title}</StyledSubheader>
        <StyledSubheader3>{date}</StyledSubheader3>
        <p>{description}</p>
      </article>
    </section>
  )
}

function Events() {
  return (
    <>
      <StyledHeader>Recent Activity</StyledHeader>
      <ul>
        {events.map(function (event, index) {
          return (
            <li key={`event-${index}`}>
              <Event event={event} />
            </li>
          );
        })}
      </ul>
    </>
  );
}

export default Events;